'use strict';
// Constants
var PAGE_SIZE = 20;
var selectData = [];


var deleteData = function (item) {
  WUI.ModalDialog.create({
    message: '11111',
    title: '2222',
    onConfirm: function () { console.log('delete data', item); }
  });
};



var change = function(item){
  var dialog = WUI.ModalDialog.create({
    width : '40%',
    title:'脱敏规则定义'
  });
  WUI.ajax({
    url: '/warehouse/maskRules',
    method: 'GET'
  }).done(function (resp) {
    var selectList = '';
    if(selectData != ''){
      selectData.map(function(sub){
        if(sub.colName == item.name){
           selectList = sub;
        }
      })
    }
    WUI.SensitiveDialog.create({
      selectData : resp.data,
      $el: $('.modal-body'),
      selectList : selectList != '' ? selectList : false,
      onConfirm : function(data){
        if(data.maskRuleID == '' ){
           $('#error-message').html('请设置脱敏规则')
        }else if(data.maskParmas == ''){
          $('#error-message').html('请设置脱敏参数')
        }else{
          dialog.hide();
          var itemDir ={}
          itemDir['colName'] = item.name
          itemDir['rule'] = data
          selectData.push(itemDir)
          $('#'+item.name).text('修改规则');
          $('#sensitive-submit').removeClass("sensitive-default");
          $("#sensitive-submit").addClass("sensitive-button");
          $('#sensitive-submit').removeAttr("disabled");


        }
      }
    });

  });
}

// Load Page
WUI.ready = function () {
  var source = WUI.link().source;
  var setMenu2 = WUI.link().setMenu2;
  var setMenu3 = WUI.link().setMenu3;



  WUI.ContentHeader.create({
    $el: $('.content-header'),
    meta: [{
      name: '列表页面'
    }]
  });
  var data = {
    source:source,
    datasetType:'TABLE',
    schema:setMenu2,
    name:setMenu3,
    rows:'20'
  }
  WUI.ajax({
    url: '/data/table',
    method: 'GET',
    data:data
  }).done(function (resp) {
    WUI.ChangeTables.create({
      $el: $('.data-table'),
      titles: resp.data.titles,
      list: resp.data.data,
      onclick: function(item){
        change(item)
      }
    });
  });

  $('#sensitive-submit').click(function () {
    var dialog = WUI.ModalDialog.create({
      width : '30%',
      title:'开始脱敏',
      onConfirm: function(){
        var submitTable = $('#submit-type').val(),
            submitName = $('#submit-name').val();
        if(submitTable == '' ){
          $('#submit-error').html('请选择名称类型')
        }else if(submitName == '') {
          $('#submit-error').html('请输入表名称')
        }else{
          var dataDir = {
            "dsSource": "string",
            "dsType": "string",
            "dsSchema": "string",
            "dsTable": "string",
            "distSource": "string",
            "distType": "string",
            "distSchema": "string",
            "distName": "string",
            "rule":selectData
          };
          dialog.hide();


          //WUI.ajax({
          //  url: '/sensitive/data/mask',
          //  method: 'POST',
          //  data:dataDir
          //}).done(function (resp) {
          //
          //});

        }

      }
    });
    $('.modal-body').append(
      '<div class="row">'+
      '<div class="col-md-12 form-group">' +
      '<select id="submit-type" class="form-control"> ' +
      '<option value="" >请设置名称类型(表或视图)</option>' +
      '<option value="table" >表</option>' +
      '<option value="view" >视图</option>' +
      '</select>'+
      '</div>'+
      '<div class="col-md-12 form-group">' +
      '<input id="submit-name" style="margin-bottom: 10px" type="text" class="form-control" placeholder="请输出名称">'+
      '</div>'+
      '</div>'+
      '<div class="row">'+
      '<div class="col-md-12 form-group" id="sensitive-input">'+
      '<span style="color: red; font-size: 15px;" id="submit-error"></span>'+
      '</div>'+
      '</div>'
    )

  })

  $('#give-user').click(function(){
    var selectData = '';
    var dialog = WUI.ModalDialog.create({
      width: '50%',
      title: '授权用户',
      onConfirm:function(){
        if(selectData) {
          var userData = {
            source:source,
            datasetType:'TABLE',
            schema:setMenu2,
            name:setMenu3,
            user: selectData
          }
          WUI.ajax({
            url: '/admin/grant',
            method: 'POST',
            data: userData
          }).done(function (resp) {
            dialog.hide();
          })
        }else{
          $('#user-error').html('请输入授权用户');
        }
      }
    })
    $('.modal-body').append(
      '<div class="row">'+
      '<div class="col-md-12 form-group">'+
      '<div id="suggest">' +
      '</div>'+
      '</div>'+
      '<div class="col-md-12 form-group" id="sensitive-input">'+
      '<span style="color: red; font-size: 15px;" id="user-error"></span>'+
      '</div>'+
      '</div>'
    )

    WUI.ajax({
      url: '/admin/users',
      method: 'GET',
    }).done(function (resp) {
       var nameData = []
      resp.data.map(function(sub){
        nameData.push(sub.name);
      })
      var ms1 = $('#suggest').magicSuggest({
        placeholder: '请选择授权用户',
        data: nameData
      });
      $(ms1).on(
        'selectionchange', function(e, cb, s){
          selectData = cb.getValue()
        }
      );
    });
  })

  $('#check-risk').click(function(){
    var data= {
      source:source,
      datasetType:'TABLE',
      schema:setMenu2,
      name:setMenu3,
      columns:'20'
    }
    WUI.ajax({
      url: '/warehouse/privacyRisk',
      method: 'GET',
      data:data
    }).done(function (resp) {
      $('.data-filter').empty();
       var list = resp.data;
      WUI.riskTable.create({
        $el: $('.data-filter'),
        list:list
      })

    })
  })

  $('#delete-table').click(function(){
    var dialog = WUI.ModalDialog.create({
      width: '30%',
      title: '删除视图/表',
      message:setMenu3,
      onConfirm: function () {
        var deleteData = {
          source:source,
          datasetType:'TABLE',
          schema:setMenu2,
          name:setMenu3
        }
        WUI.ajax({
            url: '/warehouse/dataset/delete',
            method: 'POST',
            data:deleteData
          }).done(function (resp) {
            dialog.hide();
          })
      }

    })
  })


}
